html {
  font-size: 62.5%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

:root {
  /* font */
  --nav-font: clamp(1rem, 1.7vw, 1.2rem);
  --text-font: clamp(1.2rem, 2vw, 1.6rem);
  --header-font: clamp(2.3rem, 3.5vw, 3.3rem);
  --subhead-font: clamp(1.6rem, 2.5vw, 2.4rem);
  --title-font: clamp(1.3rem, 2.2vw, 2rem);

  --ex-small-font: 0.7rem;
  --small-font: 1rem;
  --medium-font: 1.5rem;
  --large-font: 2rem;
  --ex-large-font: 2.5rem;

  /* color */
  --background-color: #111;
  --dark-color: #111;
  --text-color: #fff;
  --primary-black: #1113;
  --secondary-black: #1115;
  --dark-black: #111;
  --header-background: #111;
  --light-header-background: #fff9;
  --nav-color: orange;
  --btn-color: var(--nav-color);

  /* rgb */
  --dark-rgb: rgb(25, 25, 112, 0.2);
  --light-rgb: rgb(255, 255, 255, 0.9);

  /* transiton */
  --fast-transition: 0.3s;
  --normal-transition: 0.6s;
  --slow-transition: 1s;
}

section {
  margin-top: 1em;
}

h1 {
  font-size: var(--header-font);
}

h2 {
  font-size: var(--subhead-font);
  padding: 1em;
}

p {
  font-size: var(--text-font);
}

li {
  list-style: none;
}

input,
textarea {
  outline: none;
  cursor: pointer;
}

a {
  text-decoration: none;
}

a,
button {
  font-size: var(--nav-font);
  cursor: pointer;
}

button {
  background-color: var(--nav-color);
  color: var(--text-color);
  border: none;
  border-radius: 0.1em;
  opacity: 0.9;
  font-family: inherit;
  transition: opacity,
    var(--normal-transition) cubic-bezier(0.39, 0.575, 0.565, 1);
}

button:hover {
  opacity: 1;
}

::placeholder {
  color: var(--nav-color);
}

body {
  background: linear-gradient(var(--background-color), var(--background-color));
  background-repeat: no-repeat;
  color: var(--text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Header */
header.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  background-color: var(--header-background);
  color: var(--light-color);
  position: sticky;
  top: 0;
  z-index: 2;
  transition: background-color, color, box-shadow,
    var(--slow-transition) cubic-bezier(0.39, 0.575, 0.565, 1);
}

div.brand-name-container {
  text-align: center;
  font-style: italic;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  flex: 0.7;
}

.brand-name-container span {
  display: inline-block;
  transition: trasnform,
    var(--normal-transition) cubic-bezier(0.47, 0, 0.745, 0.715);
  animation: left-slide-in 1.3s ease-in-out;
}

@keyframes left-slide-in {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

.brand-name-container small {
  display: inline-block;
  transform: translateX(100%);
  transition: trasnform,
    var(--normal-transition) cubic-bezier(0.47, 0, 0.745, 0.715);
  animation: right-slide-in 1.3s ease-in-out forwards;
}

@keyframes right-slide-in {
  to {
    transform: translateX(0);
    text-decoration: underline;
  }
}

nav.nav-container .active {
  display: inline-block;
  transform: scale(1.3);
  font-weight: bold;
}

nav.nav-container {
  flex: 1;
}

nav.nav-container ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--text-color);
  margin: 2em;
  box-shadow: 0 0.4em 0.7em var(--background-color);
  border-radius: 0.3em;
  gap: 3em;
  padding: 1em;
  transition: background-color, color, box-shadow,
    var(--slow-transition) cubic-bezier(0.39, 0.575, 0.565, 1);
}

.nav-container ul li a {
  color: var(--nav-color);
  position: relative;
  transition: width,
    var(--normal-transition) cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.nav-container ul li a:hover::after {
  content: "";
  position: absolute;
  top: auto;
  bottom: -0.2em;
  right: 0;
  left: 0;
  width: 10%;
  height: 0.14em;
  background-color: var(--text-color);
  animation: nav-underline var(--slow-transition)
    cubic-bezier(0.47, 0, 0.745, 0.715);
}

@keyframes nav-underline {
  from {
    width: 100%;
  }
}

.onscroll {
  background-color: var(--light-header-background) !important;
  color: var(--dark-color) !important;
  box-shadow: 0 0.4em 0.7em var(--text-color);
  backdrop-filter: blur(5px);
}

/* Footer */
footer {
  margin-top: 2em;
  background-color: var(--light-rgb);
  color: var(--dark-color);
}

div.footer-container {
  display: grid;
  grid-template-columns: 1fr;
  width: max(90%, 80%);
  margin: auto;
}

footer h3 {
  text-align: center;
  padding: 1em;
}

footer span,
.footer-contact-info {
  text-align: center;
}

div.footer-nav ul li a {
  color: var(--nav-color);
}

div.footer-links ul {
  display: flex;
  justify-content: space-around;
  margin-bottom: 1em;
}

.footer-links ul li a {
  color: var(--dark-color);
}

.footer-nav ul {
  display: flex;
  justify-content: space-around;
}

div.footer-bottom {
  background-color: var(--dark-color);
  color: var(--text-color);
  padding: 2em;
}

div.footer-bottom a {
  color: inherit;
}
