section.about-section {
  opacity: 0;
  transform: translateX(-50%);
  animation: fade-in var(--slow-transition) ease-in-out forwards;
}

@keyframes fade-in {
  to {
    opacity: 1;
    transform: translateX(0%);
  }
}

div.about-img-container {
  height: 50vh;
  position: relative;
}

.about-img {
  width: 100%;
  height: 100%;
}

div.about-brand-info {
  width: max(90%, 80%);
  margin: auto;
  text-align: center;
}

.about-section h2 {
  text-align: center;
  text-decoration-skip: ink;
  clear: both;
  text-decoration: underline;
  text-decoration-color: var(--nav-color);
  -moz-text-decoration-color: var(--nav-color);
}

.about-content {
  width: max(90%, 80%);
  margin: auto;
}

.about-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  justify-content: center;
  align-items: center;
  gap: 1em;
  width: max(80%, 70%);
  margin: auto;
}

.networking-img {
  width: clamp(80%, 100%, 30em);
  height: clamp(100%, 80%, 30em);
  border-radius: 0.4em;
  box-shadow: 0 0.3em 0.5em var(--dark-color);
}

.about-section ul {
  width: max(80%, 70%);
  float: right;
  padding: 1em;
}

.about-section li {
  list-style-type: disc;
  padding: 0.3em;
  text-shadow: 0 0.1em 0.2em var(--text-color);
}

.about-section li::marker {
  color: var(--nav-color);
}

.about-testimonial-container {
  width: max(70%, 70%);
  margin: auto;
}

.testimonial-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 2em;
  box-shadow: 0 0.3em 0.7em var(--dark-color);
  transition: box-shadow, transform,
    var(--slow-transition) cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.testimonial-item:hover {
  box-shadow: 0 0.2em 0.3em var(--nav-color);
  transform: translateY(-0.3em);
}

.testimonial-item h5 {
  background-color: var(--nav-color);
  padding: 0.5em;
  align-self: flex-end;
  margin-top: 0.5em;
}

div.about-form-container {
  border-radius: 0.2em;
  padding: 2em;
  margin-top: 2em;
  background-color: var(--dark-color);
}

.about-form-container h3 {
  text-align: center;
  padding: 0.4em;
}

form.about-form:not(h3) {
  display: flex;
  justify-content: space-around;
  width: max(70%, 60%);
  margin: auto;
}

.about-form input {
  padding: 0.8em;
  flex: 1;
  background-color: #3336;
  border: none;
}

.about-form button {
  padding: 0.8em;
  margin-left: 0.4em;
}

@media (min-width: 830px) {
  div.about-img-container {
    height: 70vh;
  }
}
