section.contact-page {
  opacity: 0;
  transform: translateX(50%);
  animation: fade-in var(--slow-transition) ease-in-out forwards;
}

@keyframes fade-in {
  to {
    opacity: 1;
    transform: translateX(0%);
  }
}

div.contact-img-container {
  height: 50vh;
  position: relative;
}

.overlay {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(0, 0, 0, 0.1);
  box-shadow: inset 100px 100px 200px rgb(0, 0, 0, 0.8),
    inset 0px -290px 200px rgb(0, 0, 0, 0.8);
}

.contact-img {
  width: 100%;
  height: 100%;
}

form.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: max(80%, 70%);
  margin: auto;
  padding: 1em;
}

.contact-title {
  text-align: center;
}

.contact-form input,
.contact-form textarea {
  border-radius: 0.2em;
  transition: box-shadow, var(--normal-transition) ease-in-out;
  background-color: #3336;
}

.contact-form input:hover,
.contact-form textarea:hover {
  box-shadow: 0 0.2em 0.5em var(--dark-rgb);
}

form input::placeholder,
form textarea::placeholder {
  transition: transform, var(--normal-transition) ease-in-out;
}

form input:hover::placeholder,
form textarea:hover::placeholder {
  transform: translateY(-0.3em);
}

.contact-form input,
.contact-form textarea,
.contact-form button {
  margin-top: 1em;
  padding: clamp(1.5em, 1.8em, 2.3em);
  resize: none;
  border: none;
  transition: padding, var(--normal-transition) ease-in-out;
}

.error-message {
  text-align: center;
  width: max(80%, 70%);
  margin: auto;
  padding: 1em;
  font-size: var(--nav-font);
  background-color: var(--nav-color);
  color: var(--light-color);
  font-size: var(--nav-font);
}

div.contact-details-container {
  width: max(80%, 70%);
  margin: auto;
}

div.contact-extra-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  justify-content: space-between;
  box-shadow: 0.2em 0.4em 0.8em var(--dark-rgb);
  gap: 2em;
  -webkit-gap: 2em;
}

.contact-extra-details a {
  color: var(--text-color);
  background-color: var(--nav-color);
  padding: 0.4em 0.6em;
  width: 100%;
  display: inline-block;
  text-align: center;
  border-radius: 0.1em;
}

.contact-whatsapp,
.contact-phone {
  display: grid;
  grid-template-columns: 1fr 1fr;
  place-items: center;
  gap: 1em;
  -webkit-gap: 1em;
  border: 0.1em solid var(--nav-color);
  border-radius: 0.4em;
  transition: transform, var(--normal-transition) ease-in-out;
}

.contact-whatsapp:hover,
.contact-phone:hover {
  transform: translateY(-0.3em);
}

.contact-icon img {
  width: 100%;
  height: 100%;
}

.contact-details-title {
  text-align: center;
  padding: 1em;
}

@media (min-width: 830px) {
  div.contact-img-container {
    height: 70vh;
  }
}
