/* Hero-Section */
section.hero-section {
  padding: 0;
  z-index: -1;
}

div.hero-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  height: 70vh;
}

.hero-img-container {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.hero-img {
  width: 100%;
  height: 100%;
  opacity: 0.4;
  animation: fade-in 1s ease-in-out forwards;
}

@keyframes fade-in {
  to {
    opacity: 1;
  }
}

.hero-text-container {
  animation: slideInFromLeft 1s ease-in-out;
  transition: opacity, transform, var(--normal-transition) ease-in-out;
}

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.hero-text-container,
.hero-social-links-container {
  position: relative;
  z-index: 1;
  text-align: center;
}

.hero-social-links-container {
  animation: slideInFromRight 1s ease-in-out;
  transition: opacity, transform,
    var(--normal-transition) 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

@keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.hero-text {
  color: var(--light-color);
  -webkit-text-stroke: 0.03em;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0.3em 0.6em var(--text-color);
  transition: -webkit-text-fill-color, var(--slow-transition) linear;
  cursor: default;
  width: max(70%);
  margin: auto;
}

.hero-text:hover {
  -webkit-text-fill-color: var(--light-color);
}

.links-list {
  display: flex;
}

.links-list li {
  margin-top: 1em;
  margin-left: 2em;
}

.links-list li a {
  color: var(--light-color);
  transition: transform,
    var(--normal-transition) cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.links-list li a:hover {
  transform: translateY(-0.4em);
  display: inline-block;
}

div.hero-btn-container {
  position: absolute;
  bottom: 7em;
  z-index: 1;
  opacity: 0;
  transition: opacity,
    var(--slow-transition) cubic-bezier(0.39, 0.575, 0.565, 1);
  animation: fadeIn 1s ease-in-out 1.3s forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.hero-btn {
  padding: 1em;
  margin: 1em;
  background-color: var(--btn-color);
  color: var(--light-color);
  border: none;
  border-radius: 0.2em;
  transition: opacity, letter-spacing, var(--slow-transition) ease-in-out;
}

.hero-btn:hover {
  display: inline-block;
  letter-spacing: 0.1em;
}

div.hero-footer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-footer img {
  width: 15em;
  aspect-ratio: 16 / 9;
  z-index: 1;
  position: absolute;
  transition: opacity, transform, var(--slow-transition) ease-in-out;
  border: 0.1em solid var(--text-color);
}

.hero-footer-right {
  right: -2em;
  bottom: -2em;
  opacity: 0;
  animation: scale-up 1s ease-in-out forwards 0.5s;
}

.hero-footer-left {
  left: -2em;
  top: 8em;
  animation: scale-up 1s ease-in-out forwards;
}

.hero-footer > *:hover {
  border-radius: 4em;
  cursor: pointer;
}

@keyframes scale-up {
  0% {
    opacity: 0.4;
    transform: scale(0);
  }
  80% {
    opacity: 0.8;
    transform: scale(1);
  }
  100% {
    opacity: 1;
    transform: rotate(-8deg);
  }
}

/* Founder-section */
.founder-container {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  width: max(90%, 80%);
  margin: auto;
  gap: 2em;
  -webkit-gap: 2em;
}

.founder-section h2 {
  margin-top: 1em;
}

.founder-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 0.4em;
  box-shadow: 100px, 200px 250px rgb(0, 0, 0, 1), -100px,
    -200px 250px rgb(0, 0, 0, 1);
}

.founder-about-container {
  display: grid;
  place-items: center;
  box-shadow: 0 0.4em 0.7em var(--light-color);
  padding: 1em;
}

.founder-about-container h3 {
  padding: 0.4em;
}

.founder-about {
  text-align: center;
}

.founder-contact {
  border: 0.1em solid var(--text-color);
  padding: 0.8em 1em;
  background-color: var(--nav-color);
  color: var(--text-color);
  border-radius: 0.1em;
  transition: border, transform, var(--normal-transition) ease-in-out;
}

.founder-contact:hover {
  border: 0.1em solid var(--light-color);
  transform: scale(1.1);
}

/* Founder's statement-Section*/
.founders-statement-section h2 {
  width: max(90%, 80%);
  margin: auto;
  padding: 2em;
  transform: translateX(-100%);
  animation: slide-in 1.4s ease-in-out forwards;
}

@keyframes slide-in {
  to {
    transform: translateX(0);
  }
}

.founders-statement-intro {
  text-align: center;
  width: max(80%, 70%);
  margin: auto;
  padding: 0.7em;
}

div.founders-statement-head {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1em;
  -webkit-gap: 1em;
  width: max(70%, 60%);
  margin: auto;
  padding: 0.3em;
}

.mission,
.vision {
  padding: 1em;
  background-color: transparent;
  border: 0.1em solid var(--btn-color);
  font-size: var(--nav-font);
  transition: background-color,
    var(--slow-transition) cubic-bezier(0.47, 0, 0.745, 0.715);
}

.mission:hover,
.vision:hover {
  background-color: var(--btn-color);
}

.statement-container {
  width: max(90%, 80%);
  margin: auto;
  opacity: 0;
  transition: opacity, transform, var(--slow-transition) ease-in-out;
  animation: fade-in 1s ease-in-out forwards 0.15s;
  transform: translateX(-100%);
}

@keyframes fade-in {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.statement-title {
  text-align: center;
  padding: 2em;
}

.statement-content-container {
  display: grid;
  place-items: center;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2em;
  -webkit-gap: 2em;
  width: max(90%, 80%);
  margin: auto;
  border-radius: 0.3em;
  padding: 1em;
}

.statement-img-container {
  height: 100%;
  position: relative;
}

.img-text {
  padding: 0.3em;
}

.statement-img {
  width: 100%;
  height: 100%;
  border-radius: 0.4em;
}

.statement-text {
  padding: 1em;
  text-decoration-color: var(--btn-color);
  box-shadow: 0 0.4em 0.7em var(--background-color);
  border-radius: 0.4em;
  border: 0.1em solid var(--nav-color);
}

/* Charity Section */
section.charity-section {
  width: max(80%, 70%);
  margin: auto;
}

div.charity-container {
  display: grid;
  grid-template-columns: minmax(150px, 1fr) minmax(150px, 1fr);
  place-items: center;
  gap: 1em;
  -webkit-gap: 1em;
  padding: 1em;
  box-shadow: 0.1em 0.3em 0.4em #333;
  position: relative;
  border-radius: 0.4em;
}

.charity-img {
  width: 100%;
  height: 100%;
  margin: auto;
  -webkit-clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
  clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
  border-radius: 0.4em;
}

.charity-text-container {
  width: 100%;
  height: 100%;
}

.charity-text-container h3 {
  transform: translateX(-3em);
  padding: 1em;
  text-decoration: underline;
  text-decoration-color: var(--text-color);
  color: var(--nav-color);
}

/* Use intersectionObserver */

/* styles.css */
.fade-in {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
}

.fade-in.fade-in {
  opacity: 1;
  transform: translateY(0);
}

/* Media Qureies */

@media (min-width: 547.89px) {
  .img-text {
    position: absolute;
    top: 20%;
    right: -1em;
    font-size: var(--subhead-font);
    text-shadow: 0.2em 0.3em 0.4em var(--text-color);
    -webkit-text-stroke: 0.05em rgb(0, 0, 0, 0.6);
    -webkit-text-fill-color: transparent;
    transition: -webkit-text-fill-color, var(--slow-transition) ease-in-out;
  }

  .img-text:hover {
    -webkit-text-fill-color: var(--text-color);
  }
}

@media (min-width: 830px) {
  div.hero-wrapper {
    height: 70vh;
  }

  .founder-img {
    padding: 3em;
    transition: border-radius, var(--slow-transition) ease-in-out;
  }

  .founder-container {
    place-items: center;
  }

  section.charity-section {
    width: max(70%, 65%);
    margin: auto;
    transition: width, var(--normal-transition) ease-in-out;
  }
}
